import { IHotel } from './../home/hotel.model';
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { DetailsService } from '../services/details.service';

@Component({
    selector: "app-detail",
    templateUrl: "./detail.component.html"
})
export class HotelDetailComponent implements OnInit {

    tripid = sessionStorage.getItem("tripid");
    details: IHotel[];
    id;
    constructor(private router: Router, private detailsService: DetailsService, private route: ActivatedRoute) {

    }

    ngOnInit(): void {
        this.id = this.route.snapshot.params['id'];
        this.detailsService.getDetails(this.id).subscribe((data) => this.details = data);
    }

    onBack(): void {
        this.router.navigate(['/listing', this.tripid]);
    }
}