import { ListingService } from './../services/listing.service';
import { IHotel } from './../home/hotel.model';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

@Component({
    templateUrl: './listing.component.html',
    styleUrls: ['./listing.component.css']
})

export class ListingComponent implements OnInit {

    id: number;
    userInput: string;
    hotels: IHotel[];
    noData: string;
    roomType: any;

    constructor(
        private route: ActivatedRoute, 
        private listingService: ListingService
    ) {}

    ngOnInit(): void {
        this.id = this.route.snapshot.params['id'];
        sessionStorage.setItem("tripid", this.route.snapshot.params['id']);
        this.listingService.getHotelPerTrip(this.id).subscribe((data) => this.hotels = data)
    }

    dataReceive(roomId: string) {
        this.roomType = roomId;
    }

}