import { IHotel } from './../home/hotel.model';
import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'hotelSearch'
})
export class HotelSearchPipe implements PipeTransform {

    transform(value: IHotel[], userInput: string, roomType:string) {
        if(userInput) {
            userInput = userInput ? userInput.toLowerCase() : null;
            return userInput ? value.filter((data) =>
                ((data.name.toLowerCase().indexOf(userInput) !== -1) || (data.city_name.toLowerCase().indexOf(userInput) !== -1))
            ) : value;
        } else if(roomType) {
            return roomType ? value.filter((data) => 
                (data.roomtype.toString() == roomType.toString())) : value
        } else {
            return value;
        }
    }
}