import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
    name: 'myTextUpper'
})

export class MyTextPipe implements PipeTransform {

    transform(value: string, kind: string, text: string) {
        if(kind === "upper") {
            return value.toUpperCase();
        } else {
            return value.toLowerCase();
        }
    }
}
