import { IHotel } from './hotel.model';
import { Component, OnInit } from '@angular/core';
import { ICity } from './location.model';
import { HomeService } from '../services/home.service';
import { Router } from '@angular/router';

@Component({
    selector: 'app-search',
    templateUrl: './search.component.html',
    styleUrls: ['./search.component.css']
})

export class SearchComponent implements OnInit {
    title: string = "Plan trip with us";
    location: ICity[];
    hotels: IHotel[];

    constructor(private homeService: HomeService, private router: Router){}

    ngOnInit(): void {
        this.homeService.getCity().subscribe((data) => this.location = data);
    }

    citySelect(cityId) {
        this.homeService.getHwrtc(cityId).subscribe((data) => this.hotels = data);
    }

    hotelSelect(hotelId) {
        this.router.navigate(['/details', hotelId])
    }
    
}